import { postMessage } from '../utils/postMessage';

export const VotesPanel = ({ currentUser }) => {
  const handleVote = (event) => {
    postMessage({ currentUser, currentVote: event.target.dataset.value }, 'voted');
  };

  return (
    <div className='box options'>
      <h2>Vote</h2>
      <div className='options__buttons'>
        <button onClick={handleVote} data-value='0.5'>
          0.5
        </button>
        <button onClick={handleVote} data-value='1'>
          1
        </button>
        <button onClick={handleVote} data-value='2'>
          2
        </button>
        <button onClick={handleVote} data-value='3'>
          3
        </button>
        <button onClick={handleVote} data-value='5'>
          5
        </button>
        <button onClick={handleVote} data-value='8'>
          8
        </button>
      </div>
    </div>
  );
};
