import { postMessage } from '../utils/postMessage';

export const UserList = ({ allUsers, setAllUsers, showResults, setShowResults }) => {
  const handleResultShow = () => {
    postMessage({}, 'show_results');
  };

  const handleReset = () => {
    postMessage({}, 'reset');
    postMessage({}, 'hide_results');
  };

  return (
    <div className='box user-list'>
      <h2>User List ({allUsers.length})</h2>
      <ul>
        {allUsers.map(({ name, voted, vote }, index) => (
          <li key={index}>
            <div className='user'>
              <div className='name'>{name}</div>
              {showResults ? (
                <div className='vote'>{vote}</div>
              ) : (
                <div className='voted'>{voted ? 'Ready' : '...'}</div>
              )}
            </div>
          </li>
        ))}
      </ul>

      <button className='result-button' onClick={handleResultShow}>
        Show Results
      </button>

      <button className='reset-button' onClick={handleReset}>
        Reset Results
      </button>
    </div>
  );
};
