export const postMessage = (payload, action) => {
  try {
    fetch(`https://temp-voting-app-api.herokuapp.com/pusher`, {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        'x-event': action,
      },
      body: JSON.stringify(payload),
    });
  } catch (error) {
    console.log('Front: ', error);
  }
};
